import React from "react"
import {Provider, connect} from "react-redux"
import {HashRouter, Route} from "react-router-dom"
import ProductListContainer from "./Components/ProductList/ProductListContainer"
import HeaderContainer from "./Components/Header/HeaderContainer"
import store from "./redux/store.js"
import ProductContainer from "./Components/ProductList/ProductContainer/ProductContainer"
import {getProductListTC} from "./redux/productListReducer"
import Authorization from "./Components/Header/Authorization/Authorization"
import "./main.css"

class AppContainer extends React.Component {

   componentDidMount(props) {
      this.props.getProductListTC()
   }
   
   componentDidUpdate(prevProps) {      
      if (this.props.productList.length !== prevProps.productList.length) {
         console.log("this.props: ", this.props.productList.length)
         console.log("prevProps: ", prevProps.productList.length)
         this.props.getProductListTC()
      }
   }

   render() {
      return (
         <div className="container">
            <HeaderContainer />
            <Route exact path='/' component={ProductListContainer} />
            <Route path='/product/:id' component={ProductContainer} />
            <Route path='/authorization' component={Authorization} />
         </div>
      )
   }
}


let MapStateToProps = (state) => {
   return {
      productList: state.productListPage.productList
   }
}


let MapDispatchToProps = (dispatch) => {
   return {
      getProductListTC: () => dispatch(getProductListTC())
   }
}

const AppConnect = connect(MapStateToProps, MapDispatchToProps)(AppContainer)


const ProductCatalog = () => {
   return (
      <HashRouter>
         <Provider store={store}>
            <AppConnect />
         </Provider>
      </HashRouter>
   )
}

export default ProductCatalog
